"use client";
import React, { useState, useEffect } from "react";
import { Menu, X } from "lucide-react";
import Link from "next/link";
import { usePathname } from "next/navigation";

const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const pathname = usePathname(); // Get current path

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const navLinks = [
    { name: "Our Ink & Passion", href: "/about" },
    { name: "Our Capabilities", href: "/services" },
    { name: "The Proof Gallery", href: "/portfolio" },
  ];

  return (
    <nav
      className={`fixed w-full z-50 transition-all duration-300 ${
        isScrolled
          ? "backdrop-blur-sm bg-white/30 py-4 "
          : "bg-transparent py-6 text-neutral-900"
      }`}
    >
      <div className="max-w-11/12 mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center">
          <div className="flex items-center space-x-2">
            <div>
              <Link href={'/'} className={`text-xs  ${
                  isScrolled ? "text-gray-600" : "text-gray-200 "
                }`}
              >
                LOGO
              </Link>
            </div>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex space-x-8">
            {navLinks.map((link) => {
              const isActive = pathname === link.href;
              return (
                <Link
                  key={link.name}
                  href={link.href}
                  className={`relative font-medium text-xl transition-colors ${
                    isScrolled
                      ? isActive
                        ? "text-blue-600"
                        : "text-gray-700 hover:text-blue-600"
                      : isActive
                      ? "text-white"
                      : "text-white hover:text-blue-300"
                  }`}
                >
                  {link.name}

                  {/* SVG underline */}
                  <svg
                    className="absolute left-0 -bottom-1 w-full h-1 transition-all"
                    style={{
                      stroke: isActive ? "#2563eb" : "transparent",
                    }}
                    viewBox="0 0 100 5"
                    preserveAspectRatio="none"
                  >
                    <path
                      d="M0 2.5 Q25 -5 50 2.5 T100 2.5"
                      className="stroke-[2px] fill-transparent"
                    />
                  </svg>

                  {/* Hover Animation */}
                  <style jsx>{`
                    a:hover svg {
                      stroke: ${isScrolled ? "#2563eb" : "#93c5fd"};
                    }

                    a:hover svg path {
                      animation: wobble 0.6s infinite;
                    }

                    @keyframes wobble {
                      0% {
                        d: path("M0 2.5 Q25 -5 50 2.5 T100 2.5");
                      }
                      25% {
                        d: path("M0 2.5 Q25 0 50 2.5 T100 2.5");
                      }
                      50% {
                        d: path("M0 2.5 Q25 -5 50 2.5 T100 2.5");
                      }
                      75% {
                        d: path("M0 2.5 Q25 -10 50 2.5 T100 2.5");
                      }
                      100% {
                        d: path("M0 2.5 Q25 -5 50 2.5 T100 2.5");
                      }
                    }
                  `}</style>
                </Link>
              );
            })}
          </div>

          <div className="hidden md:flex items-center gap-4">
            <Link
              href="#contact"
              className="bg-linear-to-r from-blue-600 to-purple-600 text-white px-10 py-4 rounded-full font-medium hover:shadow-lg transform hover:-translate-y-0.5 transition-all"
            >
              Let's Connect
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <div className="md:hidden flex items-center gap-2">
            <button onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}>
              {isMobileMenuOpen ? (
                <X className={isScrolled ? "text-gray-900" : "text-white"} />
              ) : (
                <Menu className={isScrolled ? "text-gray-900" : "text-white"} />
              )}
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        {isMobileMenuOpen && (
          <div className="md:hidden mt-4 bg-white rounded-lg shadow-xl p-4">
            {navLinks.map((link) => (
              <Link
                key={link.name}
                href={link.href}
                className="block py-3 text-gray-700 hover:text-blue-600 font-medium border-b border-gray-100 last:border-0"
                onClick={() => setIsMobileMenuOpen(false)}
              >
                {link.name}
              </Link>
            ))}
            <Link
              href="#contact"
              className="block mt-4 bg-linear-to-r from-blue-600 to-purple-600 text-white px-6 py-3 rounded-full font-medium text-center"
              onClick={() => setIsMobileMenuOpen(false)}
            >
              Get in Touch
            </Link>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Header;
